.App {
	text-align: center;
    overflow-y: scroll;

	background-color: #282c34;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
    color: white;
    justify-content: stretch;
    font-size: larger;
}

.App > div {
    width: 100%;
}

.header > div.logoWrapper {
    position: relative;
    height: 25vh;
    margin-left: 20%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 80%;
}

.logo {
	height: 100%;
	pointer-events: none;
}

.header {
    position: fixed;
    top: 0px;

    width: 100%;
    height: 12vh;
    z-index: 1;
    background-color: #002147;

    display: grid;
    grid-template-columns: 20vh 30.8% 1fr;
    grid-column-gap: 3%;
    align-items: start;
    -webkit-box-shadow: 0px 11px 15px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 11px 15px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 11px 15px 0px rgba(0,0,0,0.75);
}


.header > div {
    width: 100%;
    height: inherit;
}

.buttonContainer {
    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-flow: column;
    grid-column-gap: 5%;
    align-items: center;
}

.button {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

@media only screen and (max-width: 500px) {
    div.installer {
        flex-direction: column;
    }
    div.header {
        grid-template-columns: 80% 20%;
        grid-column-gap: 0%;
    }
    div.icons {
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-auto-flow: unset;
    }
    .header > div.logoWrapper {
        height: 20vh;
    }
}

.installer {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.installer > img {
    height: 100px;
}

a.mobile {
    background-color: darkgreen;
}

.downloadLink {
    background-color: deepskyblue;
    height: 50px;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    margin: 10px;
    color: black;
    text-emphasis: none;
    text-decoration: none;
    border-radius: 25px;
}

.downloadTitle {
    font-weight: bold;
}

a:link, a:visited, a:hover, a:active {
    color: white;
    text-emphasis: none;
    text-decoration: none;
}

a.inlineLink {
    display: inline-block;
}

a.inlineLink:link, a.inlineLink:visited, a.inlineLink:hover, a.inlineLink:active {
    font-weight: bold;
    text-decoration: underline;
}

li {
    text-align: left;
}

.bold {
    font-weight: bold;
}

.header .nameWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: inherit;
}

.header div.name {
    height: auto;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    display: inline-block;
    vertical-align: middle;
    align-items: center;
    margin: 12px 0;
}
.spacer {
    min-height: 12vh;
}

#section-home {
    min-height: 0px;
}

.section {
    position: static;
    box-sizing: border-box;
    padding: 5vh 10% 5vh;
    min-height: 50vh;
    
    display: flex;
    flex-direction: column;
    word-wrap: break-word;
}

.subheader {
    margin-bottom: 5vh;
    font-size: xx-large;
}

.imageWrapper, .homeImage {
    width: 100%;
}

.subsection {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Text Gallery */

.textGallery {
    justify-items: center;
    overflow: hidden;
}

.selection {
    display: flex;
    flex-direction: row;
    justify-content: center;;
    margin: 10px 0;
}

.stage {
    position: relative;
    left: -100%;
    transition: left 1s;
    display: grid;
    grid-template-columns: 100% 100% 100%;
    justify-items: center;
}

.dot {
    height: 15px;
    width: 15px;
    background-color: #bbb;
    border-width: 2px;
    border-color: black;
    border-style: solid;
    border-radius: 50%;
    display: inline-block;
    margin: 0 5px;
}

.dotSelected {
    background-color: blue;
}

.footer {
    min-height: 25vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.icons {
    margin: 20px 0;
    display: grid;
    grid-auto-flow: column;
    column-gap: 10px;
    row-gap: 10px;
}

.icons > a {
    margin: 0 10px;
}

.icons > a > svg, .icons > a > svg > *, .icons > a > img {
    width: 50px;
    height: 50px;
    color: white;
    transition: opacity 0.25s;
    opacity: 1.0;
}

.icons > a > img {
    filter: invert(1);
}

.icons > a > svg:hover, .icons > a > img:hover {
    opacity: 0.5;
}

.author {
    margin-bottom: 20px;
}